.bootstrap-datetimepicker-widget {
  .datepicker {
    td.day.active,
    span.month.active,
    span.year.active,
    span.decade.active {
      background-color: $primary;
    }
  
    td.day.today::before {
      border-bottom-color: $primary;
    }

    td.day.active.today::before {
      border-bottom-color: #fff;
    }
  }

  .timepicker {
    .timepicker-picker {
      a.btn {
        color: $primary;

        &:hover {
          color: $primary;
        }
      }
    }
  }
}

.daterangepicker {
  .ranges {
    .active {
      background-color: $primary !important;
      font-weight: $btn-font-weight;
    }
  }
  
  td.active.start-date,
  td.active.end-date {
    background-color: $primary;
    color: $white;
    font-weight: $btn-font-weight;
  }

  td.in-range {
    background-color: #e5bfbf;
    color: $primary;
  }

  .calendar-time {
    select {
      font-weight: 600;
    }
  }

  .drp-buttons {
    .btn {
      font-weight: $btn-font-weight;
    }

    .btn.btn-default {
      background-color: transparent;
      color: $button-default-color;
    
      &:hover,
      &:active,
      &.hover {
        background-color: $light;
        color: $secondary;
      }
    
      &.disabled,
      &:disabled {
        color: $button-default-color;
        background-color: $button-default-background-color;
      }
    }

    .drp-selected {
      font-weight: 600;
    }
  }
}

@include dark-mode() {
  .daterangepicker {
    background-color: lighten($dark, 5%);
    border: inherit;

    &::before,
    &::after {
      border-bottom-color: lighten($dark, 5%);
    }

    td.available:hover,
    th.available:hover {
      background-color: lighten($dark, 5%);
    }
    td.in-range {
      background-color: lighten($dark, 10%);
      color: $white;
    }

    td.off,
    td.off.in-range,
    td.off.start-date,
    td.off.end-date {
      background-color: darken($dark, 5%);
      color: $white;
    }

    .ranges li:hover {
      background-color: $dark;
    }

    &.show-ranges.ltr .drp-calendar {
      border-color: lighten($dark, 10%);

      &.left,
      &.right {
        border-color: lighten($dark, 10%);
        padding-top: 0;
      }
    }

    .drp-buttons {
      border-color: lighten($dark, 10%);
    }

    .calendar-table {
      background-color: $dark;
      border-color: lighten($dark, 10%);

      th,
      td {
        color: $white;
      }

      .next span,
      .prev span {
        border-color: $white;
      }
    }

    select.hourselect,
    select.minuteselect,
    select.secondselect,
    select.ampmselect {
      background-color: $dark;
      border-color: lighten($dark, 10%);
    }
  }
}
