.note-editor {
    &.card {
        box-shadow: none;
    }

    .note-toolbar.card-header {
        background-color: #fff;
    }
    .note-statusbar {
        background-color: #fff !important;
    }
    .note-btn.btn-light {
        background-color: transparent;
        color: $button-default-color;
        
        &:hover,
        &:active,
        &.hover {
            background-color: $light;
            color: $secondary;
        }
        
        &.disabled,
        &:disabled {
            color: $button-default-color;
            background-color: $button-default-background-color;
        }
    }
}